@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;600&display=swap);
* {
	box-sizing: border-box;
	padding: 0;
	margin: 0;
}

a {
	text-decoration: none;
	color: lightgray;
}

body {
	font-family: 'Rubik', sans-serif;
  	-webkit-font-smoothing: antialiased;
  	-moz-osx-font-smoothing: grayscale;
	background-color: #282c34;
	color: white;
	max-width: 1100px;
	margin: 0 auto;
}


.App {
  padding: 20px;
  text-align: left;
}

.App .title {
  font-size: 4rem;
}

.won-card {
  background-color: #4da375;
}

.lost-card {
  background-color: #a34d5d;
}
.team-tile{height:160px;text-align:center;border:1px solid white;padding:20px}

.home-page .app-name{font-size:4rem;margin:30px auto}.home-page .team-grid{display:grid;grid-template-columns:1fr 1fr;grid-gap:10px;gap:10px}

.match-small-card{padding:10px;margin-bottom:20px}.match-small-card .match-result{margin-top:10px}

.team-page{display:grid;grid-template-columns:repeat(4, 1fr);grid-template-rows:repeat(3, 1fr);grid-gap:10px;gap:10px}.team-page .team-name-section{grid-column:span 3;justify-self:start;align-self:end}.team-page .team-name-section .team-name{font-size:3rem}.team-page .win-loss-section{justify-self:center;text-align:center}.team-page .match-detail-section{grid-column:span 4}.team-page .more-link{align-self:center;justify-self:center;font-size:1.5rem}

.match-detail-card{display:grid;grid-template-columns:1fr 1fr;padding:10px;margin-bottom:20px}.match-detail-card .match-date,.match-detail-card .match-venue,.match-detail-card .match-result{margin-top:10px}.match-detail-card .additional-detail{text-align:right}.match-detail-card .additional-detail>p{margin-bottom:15px}

.years-selector{text-align:center;list-style-type:none}.years-selector li{margin-top:10px;margin-bottom:10px}.years-selector li:first-child{margin-top:28px}

.match-page{display:grid;grid-template-columns:5fr 1fr}.match-page .year-selector{justify-self:end;padding:0 20px 20px 20px;margin-top:25px}.match-page .page-heading{margin:20px auto}

